:root{
    --color-white: #fff;
    --color-primary-400: #434AFA;
}

.ta-mdi{
    background: var(--color-white);height: 3rem;
}

.ta-mdi__tab{
    font-size: .875rem;
    &:hover{
        text-decoration: none;
    }
    &:not(.ta-mdi__tab--active){
        &:hover{
            background-color: rgba(67, 74, 250, 0.05);color: rgba(0,0,0,0.3);
        }
    }
}

.ta-mdi__tab--active{
    background-color: rgba(67, 74, 250, 0.1) !important;
    box-shadow: inset 0 -0.25rem 0 var(--color-primary-400);
    color: #000 !important;
    .ta-mdi__cta{
        i{opacity: 1;}
    }
}

.ta-mdi__cta{
    width: 1.25rem;height: 1.25rem;
    &:hover{background: none;}
    i{width: 1.25rem;height: 1.25rem;filter: none !important;opacity: .3}
    i.glyph.close{background: url('../images/btn-mdi-close.svg') no-repeat 0 0;}
    i.glyph.refresh{background: url('../images/btn-mdi-refresh.svg') no-repeat 0 0;}
}